.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.navbar_wrapper a{

padding-left: 30px;

}
nav a{

text-decoration: none;
color: white;

}
.delete{

background-color: red;
color:#fff;
padding:5px;
border-radius:5px;
cursor:pointer;


}
.update{

  background-color: rgb(21, 255, 0);
  color:#fff;
  padding:5px;
  border-radius:5px;
  cursor:pointer;
  


}
.App h1{

padding: 10px;

}



nav a:hover{

color: black;
transition: .6s ease-out;
letter-spacing: 2px;

}
nav *{

  padding: 0 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
